$symbols_font: "Symbols Nerd Font";
$content_width: 800px;
$border_radius: 0px;
$margin: 15px;

$base_font_size: 19px;
$title_font_mult: 1;
$main_font_mult:  1;
$mono_font_mult:  1;


    $shadow: 0 0 0 0 transparent;


body, html {
    font-family: $symbols_font, $main_font, monospace;
    font-size: $base_font_size * $main_font_mult;
    margin: 0;
    padding: 0;
    line-height: 1.3;
    background-color: var(--default_bg);
    color: var(--default_fg);

    ::selection, {
        color: var(--default_bg);
        /* background: var(--default_accent); */
        background: #ffffa9;
    }
    
    ::-moz-selection {
        color: var(--default_bg);
        /* background: var(--default_accent); */
        background: #ffff7f;
    }

}

body {
    min-height: 100vh;
}


    /* 
    a:hover {
        box-shadow: inset 100px 0 0 0 #54b3d6;
        color: white;
      } */


@mixin linkLike {
    color: var(--default_fg);
    /* text-decoration: none; */
    /* background-color: transparent; */
    /* border-radius: 0; */
    /* border-bottom: 1px solid currentcolor; */
    /* border-bottom: 1px solid; */

    text-decoration: underline;
    text-decoration-thickness: 1.5px;
    text-decoration-color: var(--default_accent);

    &:hover, &:focus {
        &, &:not([disabled]) {
             /*border-color: var(--default_accent) !important;*/
             background-color: white; 
            /* background-color: var(--default_accent); */
           /*  box-shadow: inset 100px #54b3d6; */
            color: var(--dark_bg);

        }
    }
}

@mixin roundButtonLike {
    display: inline-block;
    border-radius: $border_radius;
    background-color: var(--default_hl_bg);
    color: var(--default_fg);
    padding: ($margin / 2);
    margin: ($margin / 6) 0;
    /* transition: background-color .15s ease-in-out !important; */
    border-bottom: none;
    &:hover, &:focus {
        background-color: var(--default_accent);
    }
    box-shadow: $shadow;
}

@mixin buttonLike {
    
        @include linkLike;
    
    font-size: #{1.2 * $base_font_size};
}


a {
    @include linkLike;
}

a.unstyledLink {
    border: none;
    text-decoration: none;
    color: var(--default_fg);
}

@mixin limitWidth {
    max-width: $content_width;
    margin-left: auto;
    margin-right: auto;
}

#content {
    @include limitWidth;
    padding: $margin;
}


